import { TweenLite, Sine } from "gsap";

const draggingThreshold = 10; // px
const nextSlideThreshold = 0.1;

let down = false;
let dragging = false;
let slidePosition = 0;
let startX = 0;
let moveX = 0;
let slideCount;
let mousedown, mousemove, mouseup, mouseleave;

export default (action, el, slides, activeRef, setActive) => {
  if(action === "ADD"){
    slideCount = slides.querySelectorAll("section.home-carousel__slide").length;

    mousedown = el.addEventListener("mousedown", e => start(e, el, activeRef));
    mousemove = el.addEventListener("mousemove", e => move(e, el, slides));
    mouseup = el.addEventListener("mouseup", e => end(e, el, slides, activeRef, setActive));
    mouseleave = el.addEventListener("mouseleave", e => end(e, el, slides, activeRef, setActive));
  }

  if(action === "REMOVE"){

  }
}

const start = (e, el, activeRef) => {
  slidePosition = activeRef.current * el.offsetWidth;
  startX = slidePosition + e.pageX;
  down = true;
}

const move = (e, el, slides) => {
  const x = e.pageX;

  if(down){
    if(!dragging){
      if(x + draggingThreshold < startX || x - draggingThreshold > startX){
        dragging = true;
        el.classList.add("-dragging");
      }
    } else {
      moveX = x - startX;
      TweenLite.to(slides, 0.1, { x: moveX+"px", ease: Sine.easeOut });
    }
  }
}

const end = (e, el, slides, activeRef, setActive) => {
  if(dragging){
    const { current } = activeRef;
    const threshold = nextSlideThreshold * el.offsetWidth;

    if(
      current !== slideCount - 1
      &&
      moveX + threshold < slidePosition * -1
    ){
      setActive(current + 1);
    } else if(
      current !== 0
      &&
      moveX - threshold > slidePosition * -1
    ){
      setActive(current - 1);
    } else {
      TweenLite.to(slides, 0.4, { x: slidePosition * -1 + "px", ease: Sine.easeOut });
    }
  }

  down = false;
  dragging = false;
  el.classList.remove("-dragging");
}
